import React, { Component } from 'react';
import { connect } from 'react-redux' 
import Products from '../components/trangcon/trangsanpham/products/Products'
import Product from '../components/trangcon/trangsanpham/products/Product';
import PropTypes from 'prop-types';
import { actAddToCart, actChangeMessage } from '../actions/index'


class ProductsContainer extends Component {
  
  render() {


    var { products,keywork } = this.props;
    //search
    products = products.filter((product) => {
      return product.name.toLowerCase().indexOf(keywork.toLowerCase()) !== -1
    })
    
    return (
        <Products>
            { this.showProducts(products)}
        </Products>
    );  
        
  }

  
  showProducts(products){
    var result = null
    var {onAddToCart, onChangeMessage} = this.props 
    if(products.length > 0 ){
      result = products.map((product, index) => {
        return <Product 
        key={index} 
        product = { product }
        onAddToCart = {onAddToCart}
        onChangeMessage = {onChangeMessage}
        /> 
      })
    }
      
    return result
  }

}

ProductsContainer.propTypes = {
    products : PropTypes.arrayOf(
        PropTypes.shape({
            id : PropTypes.number.isRequired,
            name : PropTypes.string.isRequired,
            image : PropTypes.string.isRequired,
            description : PropTypes.string.isRequired,
            price : PropTypes.number.isRequired,
            model : PropTypes.string.isRequired,
            inventory : PropTypes.number.isRequired
        })
    ).isRequired,
    onChangeMessage : PropTypes.func.isRequired
}

const mapStateToProps = state => {
  return {
    products: state.products,
    keywork : state.search
  }
}

// tryền vào 2 tham số, 1 là products, 2 là số lượng
const mapDispatchToProps = (dispatch, props) => {    
    return{
      onAddToCart: (product) => {
        dispatch(actAddToCart(product, 1))
      },
      onChangeMessage : (message) => {
        dispatch(actChangeMessage(message))
      }
      
    }
}



export default connect(mapStateToProps, mapDispatchToProps) (ProductsContainer);